"use client"

import type React from "react"

import { useState } from "react"
import Link from "next/link"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Briefcase, ArrowLeft } from "lucide-react"
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card"

export default function SignupPage() {
  const router = useRouter()
  const [userType, setUserType] = useState("creative")
  const [loading, setLoading] = useState(false)

  const handleSignup = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)

    // Simulate API call
    setTimeout(() => {
      setLoading(false)
      router.push("/onboarding")
    }, 1500)
  }

  return (
    <div className="min-h-screen bg-purple-50 flex flex-col">
      <header className="border-b bg-white">
        <div className="container mx-auto px-4 py-4 flex items-center">
          <Link href="/" className="flex items-center gap-2">
            <ArrowLeft className="h-4 w-4" />
            <span className="text-sm">Back to home</span>
          </Link>
          <div className="mx-auto flex items-center gap-2">
            <Briefcase className="h-5 w-5 text-purple-600" />
            <span className="font-bold">Hustl Hub</span>
          </div>
        </div>
      </header>

      <main className="flex-1 flex items-center justify-center p-4">
        <Card className="w-full max-w-md">
          <CardHeader>
            <CardTitle className="text-2xl text-center">Create your account</CardTitle>
            <CardDescription className="text-center">Join Hustl Hub to start finding opportunities</CardDescription>
          </CardHeader>
          <CardContent>
            <Tabs defaultValue="creative" className="mb-6">
              <TabsList className="grid grid-cols-2 w-full">
                <TabsTrigger value="creative" onClick={() => setUserType("creative")}>
                  I&apos;m a Creative
                </TabsTrigger>
                <TabsTrigger value="client" onClick={() => setUserType("client")}>
                  I&apos;m a Client
                </TabsTrigger>
              </TabsList>
              <TabsContent value="creative" className="pt-4">
                <p className="text-sm text-gray-500 mb-4">
                  Create a profile to showcase your skills and find gigs that match your talents.
                </p>
              </TabsContent>
              <TabsContent value="client" className="pt-4">
                <p className="text-sm text-gray-500 mb-4">
                  Post gigs and find talented students and creatives for your projects.
                </p>
              </TabsContent>
            </Tabs>

            <form onSubmit={handleSignup} className="space-y-4">
              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="firstName">First name</Label>
                  <Input id="firstName" placeholder="John" required />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="lastName">Last name</Label>
                  <Input id="lastName" placeholder="Doe" required />
                </div>
              </div>

              <div className="space-y-2">
                <Label htmlFor="email">Email</Label>
                <Input id="email" type="email" placeholder="john@example.com" required />
              </div>

              <div className="space-y-2">
                <Label htmlFor="password">Password</Label>
                <Input id="password" type="password" placeholder="••••••••" required />
                <p className="text-xs text-gray-500">
                  Must be at least 8 characters with a number and special character
                </p>
              </div>

              {userType === "creative" && (
                <div className="space-y-2">
                  <Label htmlFor="university">University (optional)</Label>
                  <Input id="university" placeholder="e.g. UT Austin" />
                </div>
              )}

              {userType === "client" && (
                <div className="space-y-2">
                  <Label htmlFor="company">Company/Organization</Label>
                  <Input id="company" placeholder="e.g. Acme Inc." required />
                </div>
              )}

              <Button type="submit" className="w-full bg-purple-600 hover:bg-purple-700" disabled={loading}>
                {loading ? "Creating account..." : "Create account"}
              </Button>
            </form>
          </CardContent>
          <CardFooter className="flex justify-center">
            <p className="text-sm text-gray-500">
              Already have an account?{" "}
              <Link href="/login" className="text-purple-600 hover:underline">
                Log in
              </Link>
            </p>
          </CardFooter>
        </Card>
      </main>
    </div>
  )
}
